@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  background-color: #f3f2f1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: sans-serif;
  /* background-image: url("images/background.gif");
  background-size: contain; */
}

.App-welcome, #aboutme{
  opacity: 0;
  -webkit-animation: fadeInAnimation 1.5s ease 0s 1 forwards;
          animation: fadeInAnimation 1.5s ease 0s 1 forwards;
}

.App-welcome {
  display:flex;
  justify-content: center;
}

#name {
  font-family: 'Source Code Pro', monospace;
  border-right: solid 3px rgba(0, 0, 0, 0.75);
  white-space: nowrap;
  overflow: hidden;    
}

#aboutme {
  font-size: large;
  margin: 0px 25% 0px 25%;
}

#profile {
  border-radius: 50%;
  margin-top: 40px;
  height: 150px;
  width: auto;
}

/* Animation */
#name {
  -webkit-animation: animated-text 1.25s linear 1s 1 normal both,
             animated-cursor 1s steps(29,end) infinite;
          animation: animated-text 1.25s linear 1s 1 normal both,
             animated-cursor 1s steps(29,end) infinite;
}

@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* text animation */
@-webkit-keyframes animated-text{
  from{width: 0;}
  to{width: 7em;}
}
@keyframes animated-text{
  from{width: 0;}
  to{width: 7em;}
}

/* cursor animations */
@-webkit-keyframes animated-cursor{
  from{border-right-color: rgba(0, 0, 0, 0.75);}
  to{border-right-color: transparent;}
}
@keyframes animated-cursor{
  from{border-right-color: rgba(0, 0, 0, 0.75);}
  to{border-right-color: transparent;}
}
